import { ModuleWithProviders, NgModule } from '@angular/core';
import { LearnConfigService } from 'src/@learn/services/config/config.service';
import { LEARN_APP_CONFIG } from 'src/@learn/services/config/config.constants';

@NgModule()
export class LearnConfigModule
{
    /**
     * Constructor
     *
     * @param {LearnConfigService} _learnConfigService
     */
    constructor(
        private _learnConfigService: LearnConfigService
    )
    {
    }

    /**
     * forRoot method for setting user configuration
     *
     * @param config
     */
    static forRoot(config: any): ModuleWithProviders<LearnConfigModule>
    {
        return {
            ngModule : LearnConfigModule,
            providers: [
                {
                    provide : LEARN_APP_CONFIG,
                    useValue: config
                }
            ]
        };
    }
}
