import { APP_INITIALIZER, ModuleWithProviders, NgModule } from '@angular/core';
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import { LearnMockApiInterceptor } from 'src/@learn/lib/mock-api/mock-api.interceptor';
import { LearnMockApiService } from 'src/@learn/lib/mock-api/mock-api.service';

@NgModule({
    providers: [
        LearnMockApiService,
        {
            provide : HTTP_INTERCEPTORS,
            useClass: LearnMockApiInterceptor,
            multi   : true
        }
    ]
})
export class LearnMockApiModule
{
    /**
     * forRoot method for setting user configuration
     *
     * @param mockDataServices
     */
    static forRoot(mockDataServices: any[]): ModuleWithProviders<LearnMockApiModule>
    {
        return {
            ngModule : LearnMockApiModule,
            providers: [
                {
                    provide   : APP_INITIALIZER,
                    deps      : mockDataServices,
                    useFactory: () => () => null,
                    multi     : true
                },
            ]
        };
    }
}
