import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { RouterModule, ExtraOptions, PreloadAllModules  } from '@angular/router';
import { HttpClientModule } from '@angular/common/http';

import { LearnModule } from 'src/@learn/learn.module';
import { LearnConfigModule } from 'src/@learn/services/config/config.module';
import { LearnMockApiModule } from 'src/@learn/lib/mock-api';
import { CoreModule } from 'src/app/core/core.module';
import { SharedModule } from './shared/shared.module';

import { appConfig } from 'src/app/core/config/app.config';
import { mockDataServices } from 'src/app/data/mock';

