import { Injectable } from '@angular/core';
import { LearnMockApiRequestHandler } from 'src/@learn/lib/mock-api/mock-api.request-handler';

@Injectable({
    providedIn: 'root'
})
export class LearnMockApiService
{
    requestHandlers: any;

    /**
     * Constructor
     */
    constructor()
    {
        // Set the defaults
        this.requestHandlers = {
            delete: new Map<string, LearnMockApiRequestHandler>(),
            get   : new Map<string, LearnMockApiRequestHandler>(),
            patch : new Map<string, LearnMockApiRequestHandler>(),
            post  : new Map<string, LearnMockApiRequestHandler>(),
            put   : new Map<string, LearnMockApiRequestHandler>()
        };
    }

    // -----------------------------------------------------------------------------------------------------
    // @ Public methods
    // -----------------------------------------------------------------------------------------------------

    /**
     * Register 'delete' request handler
     *
     * @param url
     * @param delay
     */
    onDelete(url: string, delay: number = 0): LearnMockApiRequestHandler
    {
        return this._registerRequestHandler('delete', url, delay);
    }

    /**
     * Register 'get' request handler
     *
     * @param url
     * @param delay
     */
    onGet(url: string, delay: number = 0): LearnMockApiRequestHandler
    {
        return this._registerRequestHandler('get', url, delay);
    }

    /**
     * Register 'patch' request handler
     *
     * @param url
     * @param delay
     */
    onPatch(url: string, delay: number = 0): LearnMockApiRequestHandler
    {
        return this._registerRequestHandler('patch', url, delay);
    }

    /**
     * Register 'post' request handler
     *
     * @param url
     * @param delay
     */
    onPost(url: string, delay: number = 0): LearnMockApiRequestHandler
    {
        return this._registerRequestHandler('post', url, delay);
    }

    /**
     * Register 'put' request handler
     *
     * @param url
     * @param delay
     */
    onPut(url: string, delay: number = 0): LearnMockApiRequestHandler
    {
        return this._registerRequestHandler('put', url, delay);
    }

    // -----------------------------------------------------------------------------------------------------
    // @ Private methods
    // -----------------------------------------------------------------------------------------------------

    /**
     * Register a request handler
     *
     * @param requestType
     * @param url
     * @param delay
     * @private
     */
    private _registerRequestHandler(requestType, url, delay): LearnMockApiRequestHandler
    {
        // Create a new instance of LearnMockApiRequestHandler
        const learnMockHttp = new LearnMockApiRequestHandler();

        // Store the url
        learnMockHttp.url = url;

        // Store the delay
        learnMockHttp.delay = delay;

        // Store the request handler to access them from the interceptor
        this.requestHandlers[requestType].set(url, learnMockHttp);

        // Return the instance
        return learnMockHttp;
    }
}
