import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';
import { NoAuthGuard } from 'src/app/core/auth/guards/noAuth.guard';

import { InitialDataResolver } from 'src/app/app.resolvers';

import { LayoutComponent } from 'src/app/layout/layout.component';


export const appRoutes: Routes = [

  { path: '', pathMatch : 'full', redirectTo: 'login' },

  // Redirect signed in user to the '/dashboards/finance'
  { path: 'signed-in-redirect', pathMatch : 'full', redirectTo: 'user/add' },

  {
    path: 'login',
    component: LayoutComponent,
    data: {
      layout: 'empty'
    },
    loadChildren: () => import('src/app/modules/authentication/login/login.module').then(m => m.LoginModule)
  },

  // Admin routes
  {
    path       : '',
    component  : LayoutComponent,
    children   : [
      {
        path: 'user',
        children: [
          { path: 'add', loadChildren: () => import('src/app/modules/admin/user/add-user/add-user.module').then(m => m.AddUserModule) }
        ]
      }
    ]

  }


];

@NgModule({
  imports: [RouterModule.forChild(appRoutes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
