import { NgModule, Optional, SkipSelf } from '@angular/core';
import { MAT_FORM_FIELD_DEFAULT_OPTIONS } from '@angular/material/form-field';
import { LearnSplashScreenModule } from 'src/@learn/services/splash-screen/splash-screen.module';
import { LearnMediaWatcherModule } from 'src/@learn/services/media-watcher/media-watcher.module';

@NgModule({
    imports  : [
      LearnSplashScreenModule,
      LearnMediaWatcherModule
    ],
    providers: [
        {
            // Use the 'fill' appearance on form fields by default
            provide : MAT_FORM_FIELD_DEFAULT_OPTIONS,
            useValue: {
                appearance: 'fill'
            }
        }
    ]
})
export class LearnModule
{
    /**
     * Constructor
     *
     * @param parentModule
     */
    constructor(
        @Optional() @SkipSelf() parentModule?: LearnModule
    )
    {
        if ( parentModule )
        {
            throw new Error('LearnModule has already been loaded. Import this module in the AppModule only!');
        }
    }
}
